import Vue from 'vue';
import App from "./App.vue";
import router from "./router";
import ElementUI from 'element-ui';
import VueI18n from 'vue-i18n';
import { messages } from "./components/common/i18n";
import 'element-ui/lib/theme-chalk/index.css'; // 默认主题
// import './assets/css/theme-green/index.css'; // 浅绿色主题

import "./assets/css/icon.css";
import "./components/common/directives";
import 'babel-polyfill';
import axios from 'axios';
Vue.prototype.$http = axios;
Vue.config.productionTip = false;
Vue.use(VueI18n);
Vue.use(ElementUI, {
  size: 'small'
});
var i18n = new VueI18n({
  locale: 'zh',
  messages: messages
}); //使用钩子函数对路由进行权限跳转

router.beforeEach(function (to, from, next) {
  document.title = "".concat(to.meta.title, " | vue-manage-system");
  var role = localStorage.getItem('ms_username');

  if (!role && to.path !== '/login') {
    next('/login');
  } else if (to.meta.permission) {
    // 如果是管理员权限则可进入，这里只是简单的模拟管理员权限而已
    role === 'admin' ? next() : next('/403');
  } else if (role === 'admin' && to.path === '/reserve') {
    next('/403');
  } else {
    // 简单的判断IE10及以下不进入富文本编辑器，该组件不兼容
    if (navigator.userAgent.indexOf('MSIE') > -1 && to.path === '/editor') {
      Vue.prototype.$alert('vue-quill-editor组件不兼容IE10及以下浏览器，请使用更高版本的浏览器查看', '浏览器不兼容通知', {
        confirmButtonText: '确定'
      });
    } else {
      next();
    }
  }
});
new Vue({
  router: router,
  i18n: i18n,
  render: function render(h) {
    return h(App);
  }
}).$mount('#app');
/*统一添加token*/

axios.interceptors.request.use(function (config) {
  var token = localStorage.getItem('token'); //这个是登录时存进本地环境的token
  //console.log('mainjs\'s token : ' + token);

  config.headers = {
    Authorization: token
  };
  return config;
});